import React, { useState } from 'react'
import styled from 'styled-components'
import Popup from "../Popup";


const ButtonForm = ( { width, title, height} ) =>{

    const [Status, setStatus] = useState(false);

    return (
      <StyledDittoTextButton width={width}>
        <div className="btn-option" onClick={() => setStatus(true)}>
          <Btn_option width={width} height={height}>{title}</Btn_option>
        </div>
        <div className={`box ${Status ? "active" : ""}`}>
          <Popup onClose={() => setStatus(false)} />
        </div>
      </StyledDittoTextButton>
    );
}

const Btn_option = styled.button`
  -webkit-box-align: center;
  align-items: center;
  background: linear-gradient(59deg,rgba(71,122,185,1) 0%,rgba(153,55,142,1) 35%,rgba(222,48,85,1) 64%,rgba(217,128,87,1) 100%);
  border-radius: 16px;
  box-shadow: rgba(14, 14, 44, 0.4) 0px -1px 0px inset;
  color: rgb(255, 255, 255);
  cursor: pointer;
  display: inline-flex;
  font-family: inherit;
  font-size: 16px;
  font-weight: 600;
  width: ${(props) => props.width? props.width : "unset"};
  height: ${(props) => props.height ? props.height : "32px"};
  letter-spacing: 0.03em;
  -webkit-box-pack: center;
  justify-content: center;
  outline: none !important;
  padding: 0px 16px;
  transition: background-color 0.5s ease 0s;
  border: none;
  &:hover {
    background: #000;
  }
`;

const StyledDittoTextButton = styled.div`
   
    
    letter-spacing: 0.03em;
    -webkit-box-pack: center;
    justify-content: center;
    outline: 0px;
    transition: background-color 0.2s ease 0s;
    display: flex;
    align-items: center;
    
    .box{
        display:none;
    }
    .active{
        display:block;
    }
    .btn-option{
      width: 100%
    }
`

export default ButtonForm