import React, { useCallback, useEffect, useState } from 'react'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import styled from 'styled-components'
import TopBar from './components/TopBar'
import Footer from './components/Footer'
import MobileMenu from './components/MobileMenu'
// import Popup from './components/Popup'

// import Container from './components/Container';
import Home from './view/Home'
import Farms from './view/Farms'
import Pools from './view/Pools'
import Lottery from './view/Lottery'
import Ifo from './view/Ifo'
import Nft from './view/Nft'


import Exchanges from './view/Trade/components/Exchanges'
import Pool from './view/Trade/components/Pool'
import Liquidity from './view/Trade/components/Liquidity'
import {list} from './constants';
import PopupChoose from './components/PopupChoose'



const App = () => {
  const [mobileMenu, setMobileMenu] = useState(false)

  const handleDismissMobileMenu = useCallback(() => {
    setMobileMenu(false)
  }, [setMobileMenu])

  const handlePresentMobileMenu = useCallback(() => {
    setMobileMenu(true)
  }, [setMobileMenu])

  return (
    <StyledScreen>
      <Router>
        
        <TopBar onPresentMobileMenu={handlePresentMobileMenu} />
        {/* <ButtonMenu/> */}
        {/* <PopupChoose/> */}
        <MobileMenu onDismiss={handleDismissMobileMenu} visible={mobileMenu} />
        <div className="main">
          <Switch>
            <Route path="/" exact>
              <Home title={list[0]}/>
            </Route>
            <Route path={`/farms`}>
              <Farms title={list[1]} />
            </Route>
            <Route path={`/exchanges`}>
            <Exchanges/>
            </Route>
            <Route path={`/pool`}>
              <Pool/>
            </Route>
            <Route path={`/liquidity`}>
              <Liquidity/>
            </Route>
            <Route path="/pools" exact>
              <Pools/>
            </Route>
            <Route path="/lottery" exact>
              <Lottery/>
            </Route>
            <Route path="/ifo" exact>
              <Ifo/>
            </Route>
            <Route path="/nft" exact>
              <Nft/>
            </Route>
          </Switch>
        </div>
        <Footer></Footer>
      </Router>
    </StyledScreen>
  )
}
const StyledScreen = styled.div``

export default App;
